<template>
  <div class="my-container">

    <!-- 已登录头部 -->
    <div v-if="user" class="header user-info">
      <!-- 基本信息 -->
      <div class="base-info">
        <div class="left">
          <van-image class="avatar" :src="userInfo.photo" round fit="cover" />
          <span class="name">{{ userInfo.name }}</span>
        </div>
        <div class="right">
          <van-button size="mini" type="default" round to="/user-profile">编辑资料</van-button>
        </div>
      </div>
      <div class="hobby-info">
        <van-grid>
          <van-grid-item text="头条">
            <template #icon>
              <div>{{ userInfo.art_count }}</div>
            </template>
          </van-grid-item>
          <van-grid-item text="关注">
            <template #icon>
              <div>{{ userInfo.follow_count }}</div>
            </template>
          </van-grid-item>
          <van-grid-item text="粉丝">
            <template #icon>
              <div>{{ userInfo.fans_count }}</div>
            </template>
          </van-grid-item>
          <van-grid-item text="获赞">
            <template #icon>
              <div>{{ userInfo.like_count }}</div>
            </template>
          </van-grid-item>
        </van-grid>
      </div>
      <!-- 宫格导航 -->
      <van-grid class="grid-nav mb-9" :column-num="2" clickable>
        <van-grid-item text="收藏">
          <template #icon>
            <i slot="icon" class="iconfont icon-shoucang"></i>
          </template>
        </van-grid-item>
        <van-grid-item text="历史">
          <template #icon>
            <i slot="icon" class="iconfont icon-lishi"></i>
          </template>
        </van-grid-item>
      </van-grid>

    </div>
    <div v-else class="header not-login">
      <div class="login-btn" @click="$router.push('/login')">
        <img class="mobile-img" src="~@/assets/mobile.png" alt="">
        <span class="text">登录 / 注册</span>
      </div>
    </div>
    <van-cell title="消息通知" is-link />
    <van-cell class="mb-9" title="小智同学" is-link />
    <van-cell class="logout-cell" v-if="user" title="退出登录" @click="onLogout()" clickable />
  </div>
</template>

<script>
import { getUserInfoAPI } from '@/api/index'
// 通过内置 mapState 方法将state中的数据映射到组件中
import { mapState } from 'vuex'
export default {
  data () {
    return {
      userInfo: {}
    }
  },
  created () {
    // 如果用户登录了，则请求加载用户信息数据
    if (this.user) {
      this.loadUserInfo()
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    async loadUserInfo () {
      try {
        const res = await getUserInfoAPI()
        this.userInfo = res.data.data
      } catch (error) {
        this.$toast('获取用户失败，请稍后重试')
      }
    },
    onLogout () {
      // 退出提示:在组件中需要使用 this.$dialog 来调用弹框组件
      this.$dialog.confirm({
        title: '确认退出吗？'
      }).then(() => {
        // 确认退出：清除登录状态（容器中的 user + 本地存储中的 user）
        this.$store.commit('setUser', null)
      }).catch(console.log)
    }
  }
}
</script>

<style lang="less" scoped>
.my-container {
  padding-bottom: 100px;

  .header {
    height: 361px;
    background: url("~@/assets/banner.png");
    background-size: cover;
  }

  .not-login {
    display: flex;
    justify-content: center;
    align-items: center;

    .login-btn {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .mobile-img {
        width: 132px;
        height: 132px;
        margin-bottom: 15px;
      }

      .text {
        font-size: 28px;
        color: #fff;
      }
    }
  }

  .user-info {
    .base-info {

      height: 231px;
      padding: 76px 32px 23px;
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .left {
        display: flex;
        align-items: center;

        .avatar {
          width: 132px;
          height: 132px;
          border: 4px solid #fff;
          margin-right: 23px;
        }

        .name {
          font-size: 30px;
          color: #fff;
        }
      }
    }

    .data-stats {
      display: flex;

      .data-item {
        height: 130px;
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        color: #fff;

        .count {
          font-size: 36px;
        }

        .text {
          font-size: 23px;
        }
      }
    }

    .hobby-info {
      [class *=van-hairline]::after {
        border: unset;
      }

      // 如果说，通过UI组件库设置的组件自定义类名不生效时，可以使用/deep/来进行深度设置
      /deep/.van-grid-item__content::after {
        border: unset;
      }

      /deep/.van-grid-item__content {
        background-color: unset;
        color: #fff;

        .van-grid-item__text {
          color: #fff;
        }
      }
    }

    .grid-nav {
      height: 141px;

      i.iconfont {
        font-size: 45px;
      }

      .icon-shoucang {
        color: #eb5253;
      }

      .icon-lishi {
        color: #ff9d1d;
      }

      /deep/.van-grid-item__text {
        font-size: 30px;
      }
    }

  }

  .logout-cell {
    text-align: center;
    color: #d86262;
  }

}
</style>
