import request from '../utils/request'
// 搜索联想词接口
export const getSearchSuggestions = query => {
  return request({
    method: 'GET',
    url: '/v1_0/suggestion',
    params: {
      q: query
    }
  })
}
// 搜索结果接口
export const getSearchResult = params => {
  return request({
    method: 'GET',
    url: '/v1_0/search',
    params
  })
}
